import React, { useRef, useState } from "react";
import LoadPanel from "devextreme-react/load-panel";
import Form, { SimpleItem, Label, RequiredRule } from "devextreme-react/form";

function Contacto() {
  let formRef = useRef();
  let [loading, setLoading] = useState(false);

  let handleSubmit = (e) => {
    let form = formRef.current?.instance;
    form.validate();
    if (!form.validate().isValid) return;
    setLoading(true);
    document.getElementById("gform").submit();
    setTimeout(() => {
      form.resetValues();
      setLoading(false);
    }, 3000);
  };

  // let handleSub = () => {
  //   var sendmail = require("sendmail")({
  //     smtpHost: "smtp.gmail.com",
  //     smtpPort: 25,
  //   });

  //   let form = formRef.current?.instance;
  //   const { Nombre, Empresa, Email, Mensaje } = form.option("formData");
  //   form.validate();
  //   if (!form.validate().isValid) return;

  //   let user_captcha_value = document
  //     .getElementById("user_captcha_input")
  //     .value.trim();

  //   if (validateCaptcha(user_captcha_value, false) === true) {
  //     console.log("Captcha Matched::::");
  //     sendmail(
  //       {
  //         from: "smtp.gmail.com",
  //         to: "tavogandini@gmail.com",
  //         subject: "Test sendmail",
  //         html:
  //           "Mail of test sendmail " +
  //           Nombre +
  //           " / " +
  //           Empresa +
  //           " / " +
  //           Email +
  //           " / " +
  //           Mensaje,
  //       },
  //       function (err, reply) {
  //         console.log("ERROR MAIL::", err && err.stack);
  //         console.dir(reply);
  //       }
  //     );
  //     form.resetValues();
  //   } else {
  //     console.log("Captcha Does Not Match-----");
  //   }

  //   document.getElementById("user_captcha_input").value = "";
  // };
  return (
    <React.Fragment>
      <LoadPanel visible={loading} />
      <h2 className={"content-block"}>Contacto</h2>
      <div className={"content-block"}>
        <div className={"dx-card responsive-paddings"}>
          <form
            id="gform"
            enctype="text/plain"
            action="https://docs.google.com/forms/d/1hmrJy5yOwaLSdya3ikGxOZzbPRtL1F96Ld9SMVqkBFg/formResponse?"
            target="hidden_iframe"
            onsubmit="submitted=true;"
          >
            <Form ref={formRef}>
              <SimpleItem dataField="entry.1442310628">
                <Label text="Nombre" />
                <RequiredRule message="" />
              </SimpleItem>
              <SimpleItem dataField="entry.780658474">
                <Label text="Empresa" />
              </SimpleItem>
              <SimpleItem dataField="entry.156057418">
                <Label text="Email" />
                <RequiredRule message="" />
              </SimpleItem>
              <SimpleItem dataField="entry.1598037961">
                <Label text="Mensaje" />
                <RequiredRule message="" />
              </SimpleItem>
              <SimpleItem style={{ textAlign: "right" }}>
                <input
                  type="button"
                  value="Enviar"
                  onClick={() => handleSubmit()}
                />
              </SimpleItem>
            </Form>
          </form>

          <br />
        </div>
      </div>
    </React.Fragment>
  );
}

export default Contacto;
