import "devextreme/dist/css/dx.common.css";
import "./themes/generated/theme.base.css";
import "./themes/generated/theme.additional.css";
import React, { useState } from "react";
import { HashRouter as Router } from "react-router-dom";
import "./dx-styles.scss";
import LoadPanel from "devextreme-react/load-panel";
import { NavigationProvider } from "./contexts/navigation";
import { AuthProvider, useAuth } from "./contexts/auth";
import { useScreenSizeClass } from "./utils/media-query";
import Content from "./Content";
import UnauthenticatedContent from "./UnauthenticatedContent";
import { QrScanner } from "@yudiel/react-qr-scanner";
import { Button } from "devextreme-react";

import { check, PERMISSIONS, RESULTS } from "react-native-permissions";

function App() {
  const { user, loading } = useAuth();

  if (loading) {
    return <LoadPanel visible={true} />;
  }

  if (user) {
    return <Content />;
  }

  return <UnauthenticatedContent />;
}

// eslint-disable-next-line
export default function () {
  const screenSizeClass = useScreenSizeClass();
  const [qrResult, setQrResult] = useState("No result");

  check(PERMISSIONS.IOS.LOCATION_ALWAYS)
    .then((result) => {
      switch (result) {
        case RESULTS.UNAVAILABLE:
          console.log(
            "This feature is not available (on this device / in this context)"
          );
          break;
        case RESULTS.DENIED:
          console.log(
            "The permission has not been requested / is denied but requestable"
          );
          break;
        case RESULTS.LIMITED:
          console.log("The permission is limited: some actions are possible");
          break;
        case RESULTS.GRANTED:
          console.log("The permission is granted");
          break;
        case RESULTS.BLOCKED:
          console.log("The permission is denied and not requestable anymore");
          break;
      }
    })
    .catch((error) => {
      // …
    });

  return (
    <Router>
      <AuthProvider>
        <NavigationProvider>
          <div className={`app ${screenSizeClass}`}>
            {/* <App /> */}
            <div
              style={{ textAlign: "center", width: "500px", height: "500px" }}
            >
              <QrScanner
                scanDelay={500}
                onResult={(result, error) => {
                  setQrResult(result?.text);
                  console.log(" result ::", qrResult);
                  if (!!error) console.info("error", error);
                }}
                onError={(error) => console.log(error?.message)}
              />
              <img src="./qr-code.jpg" />
              <Button
                style={{
                  display: "flow",
                  marginTop: "20px",
                  backgroundColor: "var(--primary-light)",
                  color: "#FFF",
                }}
                text={qrResult}
                primary
                onClick={() => {
                  if (qrResult !== "No result") window.location.href = qrResult;
                }}
              />
            </div>
          </div>
        </NavigationProvider>
      </AuthProvider>
    </Router>
  );
}
